body{
  background-color: aqua;
}
.app{
  background-color: brown;
  min-height: 500px;
  width: 400px;
  margin: 40px auto;
}
#to-do-form input{
  background-color: burlywood;
  border: 0;
  width: 200px;
  height: 50px;
  padding: 0 20px;
  margin: 20px;
  font-size: 20px;
  border-radius: 10px;
  color: #fff;
}
#to-do-form input::placeholder{
  color: aliceblue;
  
}
#to-do-form input:focus{
  outline: none;
}
#to-do-form button{
  height: 50px;
  width: 100px;
  border: 0px;
  border-radius: 5px;
  cursor: pointer;
  background-color: blueviolet;
  color: #fff;
  font-size: 20px;
  transition: 1s;
  font-weight: bolder;
}
#to-do-form button:hover{
  background-color: #fff;
  color: blueviolet;
}
#to-do-form button:focus{
  outline: none;
}